import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {ApplySalesorderlist} from 'api/user'
export default{
	components:{
		UniLoadMore
	},
	data(){
		return {
			tabs:[
				{
				  value: 2,
				  name: "课程"
				},{
				  value: 1,
				  name: "商品"
				},{
				  value: 3,
				  name: "套餐"
				},{
				  value: 4,
				  name: "VIP"
				}],
			index:0,
			type:2,
			query:{},
			from:1,
			list:[],
			data:{},
			page:1,
			pageCount:1,
			moreStatus:''
		}
	},
	methods:{
		changeList(i){
			this.from = i
			this.getList()
		},
		changeTabs(e){
			this.type = e.tab.value
			this.getList()
			
		},
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			ApplySalesorderlist({
				flag:this.query.flag,
				type:this.type,
				from:this.from,
				pageindex:this.page,
				pagesize:10,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pageCount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.data = res
					this.list = this.list.concat(res.data)
				}
			})
			
		}
	},
	onLoad(query) {
		this.query = query
		this.getList()
	}
}
